import Vue from 'vue'
import Vuex from 'vuex'
import { mutations } from './mutations'
import * as actions from './actions'

Vue.use(Vuex)

const state = {
  username: '',
  email: '',
  isLogged: false,
  secureToken: '',
  isAdmin: false
}

export default new Vuex.Store({
  state,
  mutations,
  actions
})



